@import url('https://fonts.googleapis.com/css2?family=Rajdhani:wght@600&display=swap');


.banner-tv {
    padding-top: 60px;
    background: #22252d url(../bg4.jpg) no-repeat;
    height: 350px;
    margin-bottom: 70px;
    background-position: center;
    background-size: cover;
    
    @media (min-width: 992px) {
        padding-top: 100px;
        height: 550px;
    }
    
    &__anime {
        margin: 0 auto;
        display: inline-block;
        width: 100%;
        position: relative;
        height: 300px;
        margin-top: 0;
        overflow: hidden;
        
        @media (min-width: 992px) {
            height: 600px;
            margin-top: -90px;
        }
        
        .elem {
            position: absolute;
            left: 50%;
            text-transform: uppercase;
            font-size: 40px;
            font-family: 'Rajdhani';
            color: #cec6a3;
            @media (min-width: 992px) {
                font-size: 90px;
            }
            &__1 {
                bottom: 0;
                animation: slidein 1s ; 
                transform: translateX(-100%);

                img {
                    width: 110px;

                    @media (min-width: 992px) {
                        width: 250px;
                    }
                }
            }
            &__2 {
                top: 140px;
                animation: slidein2 2s;
                transform: translateX(-10%);
                left: calc(50% - 50px);
                z-index: 1;
                
                @media (min-width: 992px) {
                    top: 150px;
                }
            }
            
            &__3 {
                top: 210px;
                animation: slidein3 2.5s;
                transform: translateX(-20%);
                left: calc(50% - 10px);
                
                @media (min-width: 992px) {
                    top: 230px;
                }
            }
        }
    }
}

.button--default{
    background: #282c35;
    margin-right: 20px;
    display: inline-block;
    padding: 10px 20px;
    border-radius: 50px;
    color: #fff;
    font-weight: 700;
    font: 12px;
    text-decoration: none;
}

@keyframes slidein {
    from {
        transform: translateX(-80%);
        opacity: 0;
    }
    
    to {
        transform: translateX(-100%);
        opacity: 1;
    }
}

@keyframes slidein2 {
    from {
        transform: translateX(-90%);
        opacity: 0;
    }
    
    to {
        transform: translateX(-10%);
        opacity: 1;
    }
}

@keyframes slidein3 {
    from {
        transform: translateX(-90%);
        opacity: 0;
    }
    
    to {
        transform: translateX(-20%);
        opacity: 1;
    }
}